/*============================ 
// Global styles
=============================*/
.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

section, .section {
  position: relative;
  overflow: hidden;
}

.wow {
  visibility: hidden;
}

@keyframes toBottomFromTop {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
/*===========================
//	Utility CSS.
===========================*/
.section-padding {
  padding: 120px 0;
}
@media (max-width: 991px) {
  .section-padding {
    padding: 90px 0;
  }
}
@media (max-width: 767px) {
  .section-padding {
    padding: 70px 0;
  }
}

.section-padding--two {
  padding: 130px 0;
}
@media (max-width: 767px) {
  .section-padding--two {
    padding: 100px 0;
  }
}

.section-padding--3 {
  padding: 80px 0;
}
@media (max-width: 767px) {
  .section-padding--3 {
    padding: 70px 0;
  }
}

/*================================
// para-text/normal text style
=================================*/
.circle-radius {
  border-radius: 50%;
}

.sticky-tab-style {
  position: sticky;
  top: 77px;
  z-index: 1;
  background: white;
  padding: 15px 25px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 0px 0px 8px 8px;
  transition: all 0.4s ease-in-out;
}

.animate__animated {
  opacity: 0;
  visibility: hidden;
}

.swiper-vertical {
  max-height: 500px;
}
@media only screen and (min-width: 992px) {
  .swiper-vertical {
    max-height: 400px;
  }
}
@media only screen and (min-width: 1200px) {
  .swiper-vertical {
    max-height: 300px;
  }
}

.marquee {
  width: 100%;
  overflow: hidden;
}

/** Bootstrap Row customize**/
.nice-select {
  border: none;
}
/*=============================
// Bootstrap Row gutter reset
==============================*/
@media (min-width: 1200px) {
  .row.gutters-30 {
    margin-left: -15px;
    margin-right: -15px;
  }
  .row.gutters-30 > [class^=col-] {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.testimonial-section--style-1 .swiper-pagination {
  gap: 10px;
}
.testimonial-section--style-1 .swiper-pagination .swiper-pagination-bullet {
  background: #9BA3EB;
  width: 10px;
  height: 10px;
  opacity: 1;
  transition: all 0.4s ease-in-out;
}
.testimonial-section--style-1 .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: var(--color-primary);
  width: 20px;
  border-radius: 20px;
}
.testimonial-section--style-1 .swiper-pagination-vertical.swiper-pagination-bullets, .testimonial-section--style-1 .swiper-vertical > .swiper-pagination-bullets {
  bottom: -64px;
  left: -15px;
  right: unset;
  width: 100%;
  top: unset;
}
.testimonial-section--style-1 .reviewers .swiper-vertical {
  max-height: 400px;
}
.testimonial-section--style-1 .reviewers .swiper-slide-thumb-active .testimonial-author-info {
  background-color: #FAF9FF;
}

.header-hero-wrapper {
  background: linear-gradient(92.92deg, #2405F3 9.1%, #5C1CE2 99.72%);
  position: relative;
}
.header-hero-wrapper::before {
  content: "";
  background-image: url("../images/hero-section/opacity-bg.png");
  position: absolute;
  background-size: cover;
  background-position: center;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 1rem;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
  padding: 0;
  color: var(--color-title);
