/*=============================
//  button
==============================*/
.button {
  font-size: 15px;
  font-weight: 600;
  line-height: normal;
  border-radius: 8px;
}

.button-primary {
  background-color: var(--color-primary);
  color: var(--color-white);
  position: relative;
  z-index: 0;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
}
.button-primary i {
  transition: all 0.3s ease-in-out;
}
.button-primary::before {
  content: "";
  position: absolute;
  width: 130%;
  height: 100%;
  background: rgb(26, 0, 155);
  left: -127%;
  top: 0;
  z-index: -1;
  clip-path: polygon(0 0, 100% 0, 80% 100%, 0% 100%);
  transition: all 0.4s ease-in-out;
  border-radius: inherit;
}
.button-primary:hover {
  background-color: #1e00af;
  color: var(--color-white);
}
.button-primary:hover::before {
  left: 0;
}

.button-secondary {
  background-color: var(--color-secondary);
  color: var(--color-primary);
  transition: all 0.4s ease-in-out;
  position: relative;
  overflow: hidden;
  z-index: 0;
}
.button-secondary::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -100%;
  background-color: var(--color-primary);
  width: 100%;
  height: 100%;
  transition: all 0.2s ease-in-out;
  z-index: -1;
}
.button-secondary:hover {
  color: var(--color-white);
}
.button-secondary:hover::before {
  bottom: 0;
}

.see-all-btn {
  background-color: #ECE8FD;
  padding: 16px 31px;
}
.see-all-btn:hover {
  background-color: var(--color-primary);
}

.button-transparent {
  background: #E6F0F1;
  color: var(--color-primary);
  transition: all 0.4s ease-in-out;
}
.button-transparent:hover {
  color: var(--color-white);
}
.button-transparent:hover svg {
  fill: var(--color-white);
  transition: all 0.4s ease-in-out;
}

.button-white {
  gap: 10px;
  background: var(--color-white);
  color: var(--color-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.button-white svg {
  transition: all 0.4s ease;
}

.btn-animation {
  position: relative;
  overflow: hidden;
  transition: all 0.35s;
  z-index: 0;
}
.btn-animation::before, .btn-animation::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  background-color: var(--color-primary);
  z-index: -1;
  transition: all 0.35s;
}
.btn-animation::before {
  opacity: 0.5;
}
.btn-animation::after {
  transition-delay: 0.2s;
}
.btn-animation:hover {
  color: #fff;
}
.btn-animation:hover::before, .btn-animation:hover::after {
  top: 0;
}

.e-button {
  display: inline-block;
  position: relative;
  padding: 10px;
  overflow: hidden;
}
.e-button__link {
  text-decoration: none;
  color: #fff;
  font-family: Helvetica;
  font-size: 30px;
  text-transform: uppercase;
  display: block;
  text-align: center;
  position: relative;
  padding: 10px 5px;
  border: 3px solid #fff;
}
.e-button .line {
  position: absolute;
  background-color: #fff;
}
.e-button .line--one {
  width: 1px;
  height: 100%;
  left: 0;
  top: 0;
  transform: translate3d(0px, 102%, 0px);
}
.e-button .line--two {
  width: 100%;
  height: 1px;
  left: 0;
  top: 0;
  transform: translate3d(-102%, 0px, 0px);
}
.e-button .line--three {
  width: 1px;
  height: 100%;
  right: 0;
  top: 0;
  transform: translate3d(0px, -102%, 0px);
}
.e-button .line--four {
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  transform: translate3d(102%, 0px, 0px);
}
.e-button:hover .line {
  opacity: 1;
}
.e-button:hover .line--one, .e-button:hover .line--two, .e-button:hover .line--three, .e-button:hover .line--four {
  transform: translate3d(0, 0, 0);
  transition: transform 0.3s ease-in-out;
}
.e-button:hover .line--one {
  transition-delay: 0s;
}
.e-button:hover .line--two {
  transition-delay: 250ms;
}
.e-button:hover .line--three {
  transition-delay: 500ms;
}
.e-button:hover .line--four {
  transition-delay: 750ms;
}

